import { type } from '@testing-library/user-event/dist/type';
import './QuestionCard.css'
import ChevronRight from '@mui/icons-material/ChevronRight';

function QuestionCard(props) {

    let qS = props.qS;
    let number = props.number;

    const { onQuestionItemClicked } = props;

    const handleItemClick = (newContent) => {
        onQuestionItemClicked(newContent);
    };


    return (
        <div className="carte">
            <div className="contenu">
                <div className='title'>
                    Week Questions {number}
                </div>
                <hr class="barre-horizontal"></hr>
                <div className='paragraphe'>
                    {number === 1 ? (
                        <p>{props.q1}</p>

                    ) : (



                        <ul>
                            {qS === 1 ? (
                                <li className='question'>{props.q1} <ChevronRight className='imageAnswer' titleAccess='See my answer' fontSize='20px' onClick={() => handleItemClick(number + "A")} /></li>
                            ) : (
                                <li className='question'>{props.q1}</li>
                            )}

                            {qS === 2 ? (
                                <li className='question'>{props.q2} <ChevronRight className='imageAnswer' titleAccess='See my answer' fontSize='20px' onClick={() => handleItemClick(number + "A")} /></li>
                            ) : (
                                <li className='question'>{props.q2}</li>
                            )}

                            {qS === 3 ? (
                                <li className='question'>{props.q3} <ChevronRight className='imageAnswer' titleAccess='See my answer' fontSize='20px' onClick={() => handleItemClick(number + "A")} /></li>
                            ) : (
                                <li className='question'>{props.q3}</li>
                            )}

                        </ul>
                    )}
                </div>
            </div>
        </div >
    );
}

export default QuestionCard;
