import './PaperCard.css'


function PaperCard(props) {
    return (
        <div className="carte">
            <div className="contenu">
                <div className='title'>
                    Week Paper {props.number}
                </div>
                <hr class="barre-horizontal"></hr>
                <div className='paragraphe'>
                    <p>{props.content}</p>
                </div>
            </div>
        </div >
    );
}

export default PaperCard;
