import './HomeCard.css'


function HomeCard({ onHomeItemClicked }) {

    const handleItemClick = (newContent) => {
        onHomeItemClicked(newContent);
    };

    return (
        <div className='conteneur'>
            <div className="carte-home" onClick={() => handleItemClick("WeekQ")}>
                <div className='title-home'>
                    Week Questions
                </div>

            </div >
            <div className="carte-home" onClick={() => handleItemClick("WeekP")}>
                <div className='title-home'>
                    Week Papers
                </div>

            </div >
            <div className="carte-home" onClick={() => handleItemClick("WeekA")}>
                <div className='title-home' >
                    Week Answers
                </div>

            </div >
        </div >
    );
}

export default HomeCard;
