import './ScheduleCard.css'

function ScheduleCard(props) {

    const { onScheduleItemClicked } = props;

    const handleItemClick = (newContent) => {
        onScheduleItemClicked(newContent);
    };

    let type = props.type;


    return (
        <div className="carte">
            <div className="contenu">
                <div className='title'>
                    Week {props.name}
                </div>
                <hr class="barre-horizontal"></hr>
                <div className='content'>
                    <div className='list'>
                        <ul className='listSchedule'>
                            <li>
                                <div className='weekButton' onClick={(event) => handleItemClick(event.currentTarget.textContent + type)}>2</div>
                            </li>
                            <li>
                                <div className='weekButton' onClick={(event) => handleItemClick(event.currentTarget.textContent + type)}>3</div>
                            </li>
                            <li>
                                <div className='weekButton' onClick={(event) => handleItemClick(event.currentTarget.textContent + type)}>4</div>
                            </li>
                            <li>
                                <div className='weekButton' onClick={(event) => handleItemClick(event.currentTarget.textContent + type)}>5</div>
                            </li>
                            <li>
                                <div className='weekButton' onClick={(event) => handleItemClick(event.currentTarget.textContent + type)}>6</div>
                            </li>
                            <li>
                                <div className='weekButton' onClick={(event) => handleItemClick(event.currentTarget.textContent + type)}>7</div>
                            </li>

                        </ul>

                        <br></br>

                        <br></br>
                        <ul className='listSchedule'>
                            <li>
                                <div className='weekButton' onClick={(event) => handleItemClick(event.currentTarget.textContent + type)}>8</div>
                            </li>
                            <li>
                                <div className='weekButton' onClick={(event) => handleItemClick(event.currentTarget.textContent + type)}>9</div>
                            </li>
                            <li>
                                <div className='weekButtonLarge' onClick={(event) => handleItemClick(event.currentTarget.textContent + type)}>10</div>
                            </li>
                            <li>
                                <div className='weekButtonLarge' onClick={(event) => handleItemClick(event.currentTarget.textContent + type)}>11</div>
                            </li>
                            <li>
                                <div className='weekButtonLarge' onClick={(event) => handleItemClick(event.currentTarget.textContent + type)}>12</div>
                            </li>
                            <li>
                                <div className='weekButtonLarge' onClick={(event) => handleItemClick(event.currentTarget.textContent + type)}>13</div>
                            </li>
                            <li>
                                <div className='weekButtonLarge' onClick={(event) => handleItemClick(event.currentTarget.textContent + type)}>14</div>
                            </li>

                        </ul>

                    </div>

                </div>
            </div>
        </div>
    );
}

export default ScheduleCard;
