import './NavBar.css';


import React from "react";



function NavBar({ onNavItemClicked }) {
    const handleItemClick = (newContent) => {
        onNavItemClicked(newContent);
    };

    return (
        <div className="navbar">
            <div className="logo"><a href="#" onClick={() => handleItemClick("Home")}>Matt's Portfolio</a></div>
            <ul className='links'>
                <li><a href="#" onClick={() => handleItemClick("Home")}>Home</a></li>
                <li><a href="#" onClick={() => handleItemClick("WeekQ")}>Week Questions</a></li>
                <li><a href="#" onClick={() => handleItemClick("WeekP")}>Week Papers</a></li>
                <li><a href="#" onClick={() => handleItemClick("WeekA")}>Week Answers</a></li>
            </ul>
        </div>
    )
}

export default NavBar;