import './AnswerCard.css'
import ChevronLeft from '@mui/icons-material/ChevronLeft';


function AnswerCard(props) {

    const { onAnswerItemClicked } = props;

    let number = props.number;

    const handleItemClick = (newContent) => {
        onAnswerItemClicked(newContent);
    };


    return (
        <div className="carte">
            <div className='icon'>
                <ChevronLeft className='imageAnswer2' titleAccess='Go back to questions' fontSize='20px' onClick={() => handleItemClick(number + "Q")} />
            </div>
            <div className="contenu">
                <div className='title'>
                    Week Answer {props.number}
                </div>
                <hr class="barre-horizontal"></hr>
                <div className='paragraphe'>
                    <h2>{props.question}</h2>
                    <p>{props.content}</p>
                </div>
            </div>
        </div>
    );
}

export default AnswerCard;
